"use client";

import { cn } from "@/lib/utils";
import { TopTabs } from "./top-tabs";
import { Sidebar } from "./sidebar";
import { FloatingToolbar } from "./floating-toolbar";
import { ThemeSwitcher } from "@/components/theme-switcher";

type AppShellProps = {
  children: React.ReactNode;
  activeTab?: string;
  showSidebar?: boolean;
  showFloatingToolbar?: boolean;
  className?: string;
};

export function AppShell({
  children,
  activeTab = "/dashboards",
  showSidebar = true,
  showFloatingToolbar = false,
  className,
}: AppShellProps) {
  return (
    <div className={cn("flex min-h-screen flex-col bg-page-light dark:bg-dark-page", className)}>
      <header className="flex shrink-0 items-center justify-between gap-4 border-b border-gray-200/50 px-4 py-3 dark:border-white/10">
        <TopTabs activeHref={activeTab} />
        <div className="flex items-center gap-3">
          <ThemeSwitcher />
          <button
            type="button"
            aria-label="Notifications"
            className="flex h-9 w-9 items-center justify-center rounded-full text-gray-500 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-white/10"
          />
          <button
            type="button"
            aria-label="Messages"
            className="flex h-9 w-9 items-center justify-center rounded-full text-gray-500 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-white/10"
          />
          <div className="ml-2 h-8 w-8 rounded-full bg-gray-300 dark:bg-gray-600" aria-hidden />
        </div>
      </header>

      <div className="flex flex-1 overflow-hidden">
        {showSidebar && (
          <div className="flex shrink-0 flex-col items-center py-4 pl-3">
            <Sidebar activeHref={activeTab} />
          </div>
        )}
        <main className="flex-1 overflow-auto p-4">{children}</main>
      </div>

      {showFloatingToolbar && <FloatingToolbar />}
    </div>
  );
}
